.platform{
  width: 100%;
  height: 100%;
  background-image: url("../../img/bg.png");
  padding: 41px 24px;
  .video{
    width: 803px;
    height: 479px;
  }
  #model{
    width: 100%;
    height: 100%;
    border:1px solid rgba(60,72,129,1);
    position: relative;
    .info-card{
      position: absolute;
      top: 57px;
      right: 51px;
      width: 479px;
      height: 158px;
      background:rgba(15,19,46,1);
      opacity:0.74;
      border-radius:20px;
      padding-left: 25px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      .info-container:last-child{
        margin-bottom: 0;
      }
      .info-container{
        font-size:28px;
        font-family:Source Han Sans SC;
        font-weight:400;
        margin-bottom: 10px;
        .title{
          color:rgba(255,255,255,1);
        }
        .info{
          color:#38BE1D;
          display: inline-block;
          margin-right: 20px;
        }
        .status{
          color:#38BE1D;
        }
      }
    }


    .platform-demo{
      margin-top: 0px;
      width: 100%;
      height: 100%;
      cursor: pointer;
    }
  }
}
