.status{
  width: 100%;
  .item,.item-selected{
    width: 600px;
    background: #0D132D;
    position: relative;
    border-radius: 10px;
    padding: 22px 0;
    margin-bottom: 15px;
    display: flex;
    align-items: center;
    min-height: 95px;
    cursor: pointer;
    .triangle{
      border: solid 18px;
      width: 0;
      height: 0;
      border-color: transparent #0D132D transparent transparent;
      position: absolute;
      left:-36px;
      top: 0;
      bottom: 0;
      margin: auto 0;
    }
    .left,.left-err{
      font-size:22px;
      font-family:Source Han Sans SC;
      font-weight:bold;
      padding: 0 24px;
      width: 23%;
    }
    .left-err{
      color: #F32D1E;
    }
    .left{
      color: #ffffff;
    }
    .line{
      width:1px;
      background:rgba(139,151,209,1);
      height: 80%;
      min-height: 80%;
    }
    .right{
      margin-left: 35px;
      .tip{
        color: #ffffff;
      }
      .tip-err{
        color: #F32D1E;
      }
      .tip,.tip-err{
        font-size:20px;
        font-family:Source Han Sans SC;
        font-weight:400;
        display: flex;
        margin-bottom: 14px;
        padding-right: 36px;
        .key{
          min-width: 160px;
          margin-right: 50px;
        }
      }
      .tip:last-child,.tip-err:last-child{
        margin-bottom: 0;
      }
    }
  }
  .item:hover{
    background:rgba(72,84,169,1);
    .triangle{
      border-color: transparent #4854A9 transparent transparent;

    }
  }
  .item:last-child{
    margin-bottom: 0;
  }
  .item-selected{
    background:rgba(72,84,169,1);
    .triangle{
      border-color: transparent #4854A9 transparent transparent;
    }
  }
}
