.modal{
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 9999;
  .body{
    position: absolute;
    right: 0;
    left: 0;
    top: 0;
    bottom: 0;
    border:1px solid rgba(60,72,129,1);
    background:rgba(19,26,57,1);
    border-radius:20px;
    padding: 28px;
    margin: auto;
    .header{
      display: flex;
      justify-content: space-between;
      margin-bottom: 30px;
      .title{
        font-size:30px;
        font-family:Source Han Sans SC;
        font-weight:400;
        color:rgba(255,255,255,1);
      }
      .icon{
        width: 47px;
        height: 47px;
        cursor: pointer;
      }
    }
  }
}
