.device{
  width: 1570px;
  padding-top: 20px;
  padding-left: 8px;
  background-image: url('../../img/bg.png');
  height: 100%;
  display: flex;
  .left{
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    #model{
      width: 100%;
      height: 83.8%;
      border: 1px solid #000000;
      position: relative;
    }
    .repair-btn{
      margin-top: 12px;
      width:340px;
      height:80px;
      background:rgba(243,45,30,1);
      border-radius:10px;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      .icon{
        width: 40px;
        height: 40px;
        background-image: url("../../img/repair.png");
        background-position: center;
      }
      .text{
        margin-left: 19px;
        font-size:38px;
        font-family:Source Han Sans SC;
        font-weight:400;
        color:rgba(255,255,255,1);
      }
    }
    .repair-btn:hover{
      background: #F36555;
    }
  }
  .right{
    margin-left: 38px;
    .position{
      width: 100%;
      padding-left: 25px;
      display: flex;
      align-items: center;
      margin-bottom: 36px;
      .icon{
        width: 19px;
        height: 29px;
      }
      .title{
        font-size:28px;
        font-family:Source Han Sans SC;
        font-weight:bold;
        color:rgba(255,255,255,1);
        margin-left: 17px;
      }
      .desc{
        margin-left: 35px;
        font-size:24px;
        font-family:Source Han Sans SC;
        font-weight:400;
        color:rgba(255,255,255,1);
      }
    }
  }
}
